	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Blog Hosed, Name In Lights","date":"29 Jun 2002","slug":"/2002-06-29-blog-hosed-name-in-lights/","tags":"Y2002"},"html":"<p>Yes, I did mange to wreck the database for PapaScott by doing a simple SQL update, but I had an old backup and was back up in an hour. And now I have my name in the changelog for <a href=\"http://www.movabletype.org/news/2002_06.shtml#000509\">Movable Type 2.21</a>. I didn't think it was a bug. I thought I was just being stupid.</p>"},"next":{"fields":{"title":"Pardon Our Dust","date":"28 Jun 2002","slug":"/2002-06-28-pardon-our-dust/"}},"previous":{"fields":{"title":"Penta","date":"30 Jun 2002","slug":"/2002-06-30-penta/"}}},{"node":{"fields":{"title":"Pardon Our Dust","date":"28 Jun 2002","slug":"/2002-06-28-pardon-our-dust/","tags":"Y2002"},"html":"<p>I'm in the middle of updating my templates, and plus I'd like to see if my trackback link to an entry at <a href=\"http://philringnalda.com/\">philringnalda.com</a> worked. And can I track back to myself?</p>"},"next":{"fields":{"title":"Movable Type 2.2 With MySQL and TrackBack","date":"27 Jun 2002","slug":"/2002-06-27-movable-type-2-2-with-mysql-and-trackback/"}},"previous":{"fields":{"title":"Blog Hosed, Name In Lights","date":"29 Jun 2002","slug":"/2002-06-29-blog-hosed-name-in-lights/"}}},{"node":{"fields":{"title":"Movable Type 2.2 With MySQL and TrackBack","date":"27 Jun 2002","slug":"/2002-06-27-movable-type-2-2-with-mysql-and-trackback/","tags":"Y2002"},"html":"<p><a href=\"http://www.movabletype.org/news/2002_06.shtml#000499\">Movable Type 2.2 released</a>. PapaScott has been running the beta version with the MySQL backend for the past two weeks. Nice to have the data so accessable. But the real mind bomb, as <a href=\"http://www.scripting.com\">Dave</a> would say, is the TrackBack feature, which enables entries in your blog to be pinged by other blogs linking to it, and thus create a list of those links your entry. It will be like an Internet-wide comments feature, once people grok it and other blogging systems implement it as well.</p>\n<p>It would be really nice if MT could ping both weblogs.com and blo.gs by default. But it should be easy enough to add a couple of lines to get that to work.</p>\n<p>28 Jun: Let's see if trackback is working for the daily archive.</p>"},"next":{"fields":{"title":"You Can See Me Now Or You Can See Me Later","date":"25 Jun 2002","slug":"/2002-06-25-you-can-see-me-now-or-you-can-see-me-later/"}},"previous":{"fields":{"title":"Pardon Our Dust","date":"28 Jun 2002","slug":"/2002-06-28-pardon-our-dust/"}}}],"pathPrefix":"","first":false,"last":false,"index":878,"pageCount":1101}}

